.web {
    display:flex;
    flex: 1; 
    align-items: center; 
}
.web-links {
    font-size: 16px;
    margin-right: 16px;
    font-weight: 700; 
}

.web-links a {
    text-decoration: none;
    color: black;
    display: flex;
    align-items: center;
}
.web-links:last-child {
    margin-right: 0;
}
.links-icon {
    display: flex;
    align-items:center;
    margin: 4px;
}